import { memo } from "react";
import { NodeResizer } from "reactflow";

const NodeSelectedGroup = ({ data, selected }) => {
  return (
    <>
      <NodeResizer isVisible={selected} minWidth={100} minHeight={50} />
      <p
        style={{
          fontWeight: "bold",
          fontSize: "15px",
          textAlign: "start",
          maxWidth: "150px",
          minHeight: "auto",
          verticalAlign: "middle",
          padding: "10px",
          borderRadius: "10px",
          color: "gray",
        }}
      >
        {data.label}
      </p>
      <div
        style={{
          padding: "7px",
          borderRadius: "10px",
        }}
      ></div>
    </>
  );
};

export default memo(NodeSelectedGroup);
