const nodeTypes = {
  default: ResizableNodeSelected,
  group: NodeSelectedGroup,
};

const App = () => {
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);
  const [selectedDepartaments, setSelectedDepartaments] = useState([]);
  const [selectedNode, setSelectedNode] = useState(null);

  const [nodeType, setNodeType] = useState("default");
  const [newNodeData, setNewNodeData] = useState({
    label: "",
    group: "",
    type: "default",
  });
  const [editNodeData, setEditNodeData] = useState({
    id: "",
    label: "",
    backgroundColor: "#ffffff",
  });

  const handleFileUpload = (event) => {
    const file = event.target.files[0];

    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const content = e.target.result;
        const workbook = XLSX.read(content, { type: "binary" });
        const sheetName = workbook.SheetNames[0];
        const sheet = workbook.Sheets[sheetName];

        const data = XLSX.utils.sheet_to_json(sheet);

        // Обработка данных и создание узлов и ребер
        const newNodes = [];
        const newEdges = [];
        const groupColors = new Map(); // Map для хранения цветов групп

        data.forEach((row) => {
          const groupId = row["Группа"];
          const nodeId = row["ID"];
          const nodeNumberTask = row["Номер задачи"];
          const nodeName = row["Наименование"];
          const nodeTime = row["Срок выполнения"];
          const nodeResult = row["Результат"];
          const nodeType = row["Тип узла"];
          const nodeWidth = row["Ширина узла"];
          const nodeHeight = row["Высота узла"];
          const nodePositionX = parseInt(row["Позиция узла X"], 10);
          const nodePositionY = parseInt(row["Позиция узла Y"], 10);
          const sourceNodeId = row["Идентификатор исходного узла"];
          const sourceFlout = row["Сторона исходного ребра"];
          const targetFlout = row["Сторона целевого ребра"];
          const targetNodeId = row["Идентификатор целевого узла"];
          const groupColor = row["Цвет группы"] || "#ffffff"; // Цвет по умолчанию

          // Пропуск узла без группы
          if (!groupId && !nodeId) {
            return;
          }

          // Если группа уже встречалась, обновляем ее цвет
          if (groupColors.has(groupId)) {
            groupColors.get(groupId).backgroundColor = groupColor;
          } else {
            // Создаем новую группу и сохраняем ее цвет
            groupColors.set(groupId, { backgroundColor: groupColor });
          }

          // Создание группы, если ее еще нет
          if (groupId && !newNodes.find((node) => node.id === groupId)) {
            newNodes.push({
              id: groupId,
              type: "group",
              data: { label: groupId },
              position: { x: nodePositionX, y: nodePositionY },
              isGroup: true,
              style: {
                border: "3px solid gray",
                width: nodeWidth,
                height: nodeHeight,
                zIndex: "-3",

                ...groupColors.get(groupId), // Добавляем цвет группы
              },
            });
          }

          // Создание узла
          const newNode = {
            id: nodeId,
            type: nodeType || "default",
            data: {
              label: nodeName,
              time: nodeTime,
              result: nodeResult,
              numberTask: nodeNumberTask,
            },
            position: { x: nodePositionX, y: nodePositionY },
            parentNode: groupId,
            style: {
              width: nodeWidth,
              color: "white",
              height: nodeHeight,
              zIndex: "-2",
              ...groupColors.get(groupId),
            },
          };

          // Применяем extent: "parent" только для дочерних узлов внутри группы
          if (groupId) {
            newNode.extent = "parent";
          }

          newNodes.push(newNode);

          // Создание ребра
          if (sourceNodeId && targetNodeId) {
            newEdges.push({
              id: `edge-${sourceNodeId}-${targetNodeId}`,
              source: sourceNodeId,
              sourceHandle: sourceFlout,
              target: targetNodeId,
              targetHandle: targetFlout,
              type: "smoothstep",
              isHiddenOnDrag: false,
              animated: true,
              style: { stroke: "gray", zIndex: "0", strokeWidth: 2 },
              arrowHeadType: "arrow",
              markerEnd: {
                type: "arrow",
                width: 20,
                height: 20,
                color: "gray",
              },
            });
          }
        });

        setNodes(newNodes);
        setEdges(newEdges);
      };

      reader.readAsBinaryString(file);
    }
  };

  const onConnect = useCallback(
    (params) => {
      const smoothStepEdge = {
        ...params,
        type: "smoothstep",
        animated: true,
        style: { stroke: "gray", strokeWidth: 2 },
        arrowHeadType: "ArrowClosed",
        label: "+",
        data: { customData: "some data" },
        isHiddenOnDrag: false,
        markerEnd: {
          type: "arrow",
          width: 20,
          height: 20,
          color: "gray",
        },
      };

      setEdges((eds) => addEdge(smoothStepEdge, eds));
    },
    [setEdges]
  );

  const onEdgeClick = (event, edge) => {
    const newLabel = prompt("Введите новую метку:", edge.label);

    if (newLabel !== null) {
      // Обновляем состояние ребра с новой меткой
      setEdges((prevEdges) => {
        const updatedEdges = prevEdges.map((prevEdge) =>
          prevEdge.id === edge.id ? { ...prevEdge, label: newLabel } : prevEdge
        );
        return updatedEdges;
      });
    }
  };

  const uniqueDepartaments = useMemo(() => {
    const departaments = new Set();
    nodes.forEach((node) => {
      if (node.type === "group") {
        departaments.add(node.data.label);
      }
    });
    return Array.from(departaments);
  }, [nodes]);

  const handleFilterToggle = (departament) => {
    setSelectedDepartaments((prevSelected) => {
      if (prevSelected.includes(departament)) {
        return prevSelected.filter(
          (selectedDep) => selectedDep !== departament
        );
      } else {
        return [...prevSelected, departament];
      }
    });
  };

  const filteredNodes = useMemo(() => {
    if (selectedDepartaments.length === 0) {
      return nodes;
    }
    return nodes.map((node) => {
      if (
        node.type === "group" &&
        !selectedDepartaments.includes(node.data.label)
      ) {
        // Скрываем все группы, кроме выбранных
        return {
          ...node,
          style: {
            display: "none",
          },
        };
      } else {
        return node;
      }
    });
  }, [nodes, selectedDepartaments]);

  //сохранение в файл
  const saveToFile = () => {
    const dataToSave = {
      nodes,
      edges,
    };

    const blob = new Blob([JSON.stringify(dataToSave)], {
      type: "application/json",
    });

    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "flowData.json";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const loadFromFile = (event) => {
    const file = event.target.files[0];

    if (file) {
      const reader = new FileReader();

      reader.onload = (e) => {
        const content = e.target.result;
        const data = JSON.parse(content);

        if (data && data.nodes && data.edges) {
          setNodes(data.nodes);
          setEdges(data.edges);
        }
      };

      reader.readAsText(file);
    }
  };
  //редактирование функция
  const handleNodeEdit = () => {
    setNodes((prevNodes) =>
      prevNodes.map((node) =>
        node.id === editNodeData.id
          ? {
              ...node,
              data: {
                ...node.data,
                label: editNodeData.label,
                // Обновите другие поля в соответствии с формой редактирования
              },
              style: {
                ...node.style,
                backgroundColor: editNodeData.backgroundColor,
                // Обновите другие стили по мере необходимости
              },
            }
          : node
      )
    );

    // Сбросьте выбранный узел и данные редактирования
    setSelectedNode(null);
    setEditNodeData({
      id: "",
      label: "",
      // Сбросьте другие поля редактирования
    });
  };

  // Замените этот код внутри вашего компонента App
  const addNode = () => {
    const newNode = {
      id: `${nodes.length + 1}`, // Используем длину всего массива узлов для уникального ID
      type: newNodeData.type || nodeType,
      data: { label: newNodeData.label },
      position: {
        x: Math.random() * window.innerWidth - 100,
        y: Math.random() * window.innerHeight,
      },
      parentNode: newNodeData.group, // Используем выбранное значение группы или значение по умолчанию
    };

    setNodes((prevNodes) => [...prevNodes, newNode]);
    setEdges((prevEdges) => [
      ...prevEdges,
      {
        id: `e-${newNode.id}`,
        source: "",
        target: "",
        type: "smoothstep",
        style: {
          strokeWidth: 2,
          stroke: "#f00",
          zIndex: "-2",
        },
      },
    ]);

    // Сбрасываем данные формы после добавления узла
    setNewNodeData({ label: "", group: "", type: "" });
  };

  return (
    <div className="container">
      <div className="header">
        <h3>Фильтр департаментов</h3>
        {uniqueDepartaments.map((departament) => (
          <button
            key={departament}
            onClick={() => handleFilterToggle(departament)}
            className={`filterButton ${
              selectedDepartaments.includes(departament) ? "selected" : ""
            }`}
          >
            {departament}
          </button>
        ))}
      </div>
      <div className="content">
        {/* Левая часть страницы */}
        <div className="leftPanel">
          <input type="file" onChange={handleFileUpload} />
          <button onClick={saveToFile} className="saveButton">
            Сохранить в файл json
          </button>
          <input type="file" onChange={loadFromFile} className="fileInput" />
          <div className="newNodeSection">
            <label className="newNodeLabel">Имя нового узла:</label>
            <input
              type="text"
              value={newNodeData.label || ""}
              onChange={(e) =>
                setNewNodeData({ ...newNodeData, label: e.target.value })
              }
              className="newNodeInput newNodeLabel"
            />
            <label className="newNodeGroup">Группа нового узла:</label>
            <input
              type="text"
              value={newNodeData.group}
              onChange={(e) =>
                setNewNodeData({ ...newNodeData, group: e.target.value })
              }
              className="newNodeInput newNodeGroup"
            />
            <label className="newNodeType">Тип нового узла:</label>
            <select
              value={newNodeData.type || nodeType}
              onChange={(e) =>
                setNewNodeData({ ...newNodeData, type: e.target.value })
              }
              className="newNodeInput newNodeType"
            >
              <option value="default">Default</option>
              <option value="group">group</option>
            </select>
            <button onClick={addNode} className="addNodeButton">
              Добавить узел
            </button>
          </div>
          {selectedNode && (
            <div>
              <h4>Информация о выбранном узле:</h4>
              <p>ID: {selectedNode.id}</p>
              <p>Время: {selectedNode.data.time}</p>
              <p>Результат: {selectedNode.data.result}</p>
              {/* Другие поля, которые вы хотите отобразить */}
              <hr />
              <label>Новая метка:</label>
              <input
                type="text"
                value={editNodeData.label}
                onChange={(e) =>
                  setEditNodeData({ ...editNodeData, label: e.target.value })
                }
              />
              <label>Новый цвет фона:</label>
              <input
                type="color"
                value={editNodeData.backgroundColor || "#ffffff"}
                onChange={(e) =>
                  setEditNodeData({
                    ...editNodeData,
                    backgroundColor: e.target.value,
                  })
                }
              />

              {/* Добавьте другие поля для редактирования */}
              <button onClick={handleNodeEdit}>Сохранить изменения</button>
            </div>
          )}
        </div>
        {/* Правая часть страницы */}
        <ReactFlow
          nodes={filteredNodes}
          edges={edges}
          onNodesChange={onNodesChange}
          onEdgesChange={onEdgesChange}
          onConnect={onConnect}
          onEdgeClick={onEdgeClick}
          onNodeClick={(event, node) => {
            setSelectedNode(node);
            setEditNodeData({
              id: node.id,
              label: node.data.label,
              // Установите другие поля для редактирования
            });
          }}
          nodeTypes={nodeTypes}
          className="rightPanel touchdevice-flow"
        >
          <Controls />
          <MiniMap />
          <Background variant="dots" gap={12} size={1} />
        </ReactFlow>
      </div>
    </div>
  );
};

export default App;
